    text-decoration: none;\n    font-size: 30px;\n    top: 10px;\n    display: inline-block;\n    min-width: 44px;\n    min-height: 44px;\n  }\n\n  #hamburger:visited {\n    color: #000;\n  }\n\n  #drawer-desktop {\n    display: none;\n  }\n\n  #drawer.open {\n    transform: translate(-50px,0);\n  }\n\n  #drawer {\n    text-align: left;\n    width: 250px;\n    height: 100%;\n    position: fixed;\n    top: 0;\n    transform: translate(-300px, 0);\n    transition: transform 0.3s ease-in-out;\n    background-color: #000;\n    z-index: 99999;\n  }\n\n  #drawer li {\n    display: block;\n    text-align: right;\n    padding: 7px 20px;\n  }\n\n  #drawer li a {\n    color: #fff;\n    text-decoration: none;\n    display: block;\n    padding: 13px 0;\n  }\n\n  #drawer li:first-child a {\n      color: #fff;\n      text-decoration: none;\n      display: inline-block;\n  }\n\n  #drawer img {\n    width: 100px;\n    display: inline;\n  }\n\n  .skip-link {\n    position: absolute;\n    top: -40px;\n    left: auto;\n    right: 0;\n    z-index: 99999;\n  }\n\n  .skip-link:focus {\n    top: 0;\n  }\n}\n\n/* Main Content Styling */\n.hero-banner {\n  background: #fff url(\"../public/images/heros/hero-image_1.jpg\") no-repeat 0 0;\n  background-size: cover;\n}\n\n.hero-banner {\n  min-height: 400px;\n}\n\n.hero-inner-wrapper {\n  margin: 0;\n  position: relative;\n  padding: 20px;\n  background-color: rgba(0, 0, 0, 0.6);\n  color: #fff;\n  text-align: left;\n  transform: translate(10%, 110%);\n  display: inline-block;\n}\n\n.hero-inner-wrapper h2 {\n  margin-bottom: 0;\n}\n\nmain h1 {\n  margin: 0;\n}\n\n@media screen and (max-width: 426px) {\n  main {\n    padding-top: 100px;\n  }\n}\n\n.restaurant-menu-list {\n  display: grid;\n  grid-template-columns: 30% 30% 30%;\n  grid-template-rows: auto auto;\n  justify-content: center;\n  margin: 20px 0;\n  column-gap: 20px;\n  row-gap: 20px;\n}\n\n.restaurant-menu-item {\n  border-radius: 15px;\n}\n\n.restaurant-menu-item .menu-list-top {\n  position: relative;\n  min-height: 295px;\n  max-height: 295px;\n  overflow: hidden;\n}\n\n.restaurant-menu-item .menu-list-top img {\n  width: 100%;\n  min-height: 295px;\n  max-height: 295px;\n}\n\n#detail-content .restaurant-menu-item .menu-list-top img {\n  width: 100%;\n  height: auto;\n  max-height: initial;\n}\n\n#detail-content .restaurant-menu-item .menu-list-top {\n  height: auto;\n}\n\n\n.restaurant-menu-item .menu-list-top .menu-place {\n  position: absolute;\n  font-weight: 600;\n  color: #fff;\n  background-color: rgba(0, 0, 0, 0.5);\n  padding: 7px;\n}\n\n.restaurant-menu-item .menu-list-bottom > div {\n  margin: 8px 0;\n}\n\n.restaurant-menu-item .menu-rate .fa {\n  color: orange;\n}\n\n.restaurant-menu-item .menu-name {\n  font-weight: 600;\n  font-size: 17px;\n}\n\n.menu-cta a {\n  display: block;\n  color: #fff;\n  font-weight: 600;\n  text-align: center;\n  padding: 14px;\n  text-decoration: none;\n  background-color: #8d0000;\n}\n\n@media screen and (max-width: 426px) {\n\n  header .nav-grid {\n    padding: 15px 10px 15px 0px;\n  }\n\n  .restaurant-menu-list {\n    grid-template-columns: 100%;\n    row-gap: 30px;\n  }\n\n  .restaurant-menu-item {\n    border-radius: 0;\n    border-bottom: 1px solid gray;\n    padding-bottom: 10px;\n  }\n\n  .menu-list-bottom {\n    padding: 0 12px;\n  }\n\n  .hero-inner-wrapper {\n    position: absolute;\n    transform: none;\n    top: 30%;\n    left: 0;\n  }\n\n  .restaurant-menu-item .menu-image img {\n    min-height: auto;\n    max-height: auto;\n  }\n}\n\n/* Single Post Styling */\n#detail-content {\n  padding: 30px;\n}\n\n.detail-subtitle {\n  font-size: 22px;\n  font-weight: 700;\n  margin: 10px 0;\n}\n\n#food-list, #drink-list {\n  margin-bottom: 20px;\n}\n\n.content-heading {\n  text-align: center;\n  margin: 30px;\n}\n\n.detail-grid {\n  display: grid;\n  grid-template-columns: 45% 45%;\n  column-gap: 20px;\n  padding: 15px 0;\n  border-top: 2px solid black;\n  border-bottom: 2px solid black;\n}\n\n@med